.container .search {
  width: 80px;
  height: 80px;
  z-index: 4;
  background: #dc143c;
  border: none;
  border-radius: 50%;
  margin: auto;
  transition: all 1s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 25px rgba(0, 0, 0, .4);
}

.container .search:hover {
  cursor: pointer;
}

.container .search:before {
  content: "";
  width: 12px;
  height: 2px;
  background: #fff;
  margin: auto;
  transition: all .5s;
  position: absolute;
  top: 22px;
  bottom: 0;
  left: 22px;
  right: 0;
  transform: rotate(45deg);
}

.container .search:after {
  content: "";
  width: 25px;
  height: 25px;
  border: 2px solid #fff;
  border-radius: 50%;
  margin: auto;
  transition: all .5s;
  position: absolute;
  top: -5px;
  bottom: 0;
  left: -5px;
  right: 0;
}

