  border: none;
  border-radius: 30px;
  outline: none;
  margin: auto;
  padding: 0 80px 0 20px;
  font-family: Inconsolata, monospace;
  font-weight: bolder;
  transition: all 1s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 25px #dc143c, 0 20px 25px rgba(0, 0, 0, .2);
}

.container .inp-search:hover {
  cursor: pointer;
}

.container .inp-search:focus {
  width: 300px;
  opacity: 1;
  cursor: text;
}

.container .inp-search:focus ~ .search {
  z-index: 6;
  background: #151515;
  right: -250px;
}

.container .inp-search:focus ~ .search:before {
  width: 25px;
  top: 0;
  left: 0;
}

.container .inp-search:focus ~ .search:after {
  width: 25px;
  height: 2px;
  background: #fff;
  border: none;
  border-radius: 0%;
  top: 0;
  left: 0;
  transform: rotate(-45deg);
}

