  color: #58c7fa;
  transition: color 1s;
}

.card:hover:before, .card:hover:after {
  opacity: 0;
  animation: none;
}

.card:before {
  content: "";
  width: 104%;
  height: 102%;
  background-image: linear-gradient(var(--rotate), #5ddcff, #3c67e3 43%, #4e00c2);
  z-index: -1;
  animation: spin 2.5s linear infinite;
  position: absolute;
  top: -1%;
}

.card:after {
  content: "";
  top: calc(var(--card-height) / 6);
  z-index: -1;
  height: 100%;
  width: 100%;
  filter: blur(calc(var(--card-height) / 6) );
  background-image: linear-gradient(var(--rotate), #5ddcff, #3c67e3 43%, #4e00c2);
  opacity: 1;
  margin: 0 auto;
  transition: opacity .5s;
  position: absolute;
  left: 0;
  right: 0;
  transform: scale(.8);
}

